.popup__container {
  width: 430px;
  border-radius: 10px;
  background-color: #fff;
  display: flex;
  flex-direction: column;
  align-items: center;
  box-shadow: 0px 0px 25px rgba(0, 0, 0, .15);
}

@media screen and (max-width: 470px) {
  .popup__container {
    max-width: 260px;
  }
}
